#root {
  background-image: url("./images/Website_Background.gif");
  background-size: 100vw 100vh;
  height: 100vh;
  position: relative;
}
@media (max-width: 1200px) {
  #root {
    background-image: url("./images//resized-3.png");
    background-size: 100vw 100vh;
  }
}

@font-face {
  font-family: "Spindles End";
  src: url(SpindlesEnd-YqV8.ttf) format("truetype");
}
.toolbar {
  display: flex;
  flex-direction: column;
}
.links-section {
  display: flex;
  flex-direction: row;
  min-width: 100vw;
  align-items: center;
}
@media (max-width: 1200px) {
  .links-section {
    flex-direction: column;
    padding-top: 0.5rem;
  }
}
.links {
  height: 2rem;
  width: 2rem;
  padding: 1rem;
  max-width: 33vw;
}
.link {
  height: 2rem;
  width: 2rem;
}
.connect-div {
  box-sizing: border-box;

  /* Auto layout */

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 17px 33px;
  gap: 8px;

  width: 123px;
  height: 48px;
  left: 1247px;
  top: 64px;

  background: #47280f;
  border-radius: 10px;
}

.outside-link {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  min-width: 0vw;
}
.connect {
  display: flex;
  justify-content: center;
  min-width: 90vw;
}
.inside-button {
  width: 57px;
  height: 16px;
  font-family: "Spindles End";
  font-style: normal;
  font-weight: 1000;
  font-size: 18px;
  line-height: 16px;
  color: #ffffff;
  flex: none;
  order: 0;
  flex-grow: 0;
  background: #47280f;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}
.mint-instructions {
  font-family: "Spindles End";
  font-style: normal;
  font-weight: 1000;
  font-size: 18px;
  line-height: 16px;
  color: #ffffff;
  flex: none;
  order: 0;
  flex-grow: 0;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  padding-bottom: 26vh;
  flex-direction: column;
}
@media (max-width: 1200px) {
  .mint-instructions {
    padding-bottom: 10vh;
    text-align: center;
  }
}
.inside-mint-button {
  width: 80px;
  height: 25px;
  font-family: "Spindles End";
  font-style: normal;
  font-weight: 1000;
  font-size: 21px;
  line-height: 16px;
  color: #ffffff;
  flex: none;
  order: 0;
  flex-grow: inherit;
  background: #47280f;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}
.none {
  display: none;
}

.mint-button-div {
  align-content: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin: 1rem;
  align-items: center;
  min-height: 63%;
  padding-top: 3vh;
}
@media (max-width: 1200px) {
  .mint-button-div {
    min-height: 40vh;
  }
}
@media (max-width: 1200px) {
  .mint-buttons-div {
    padding-top: 5vh;
  }
}

.mint-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 0.5rem;
}

.connect-div {
  box-sizing: border-box;

  /* Auto layout */

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 17px 33px;
  gap: 8px;

  width: 123px;
  height: 48px;
  left: 1247px;
  top: 64px;

  background: #47280f;
  border: 1px solid black;
  border-radius: 10px;
}
@media (max-width: 1200px) {
  .connect-div {
    margin-left: 0vw;
  }
}
.connect-mint {
  box-sizing: border-box;

  /* Auto layout */

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 17px 33px;
  gap: 8px;

  width: 123px;
  height: 48px;
  left: 1247px;
  top: 64px;

  background: #47280f;
  border: 1px solid black;
  border-radius: 10px;
}
@media (max-width: 1200px) {
  .connect-mint {
    width: 90px;
  }
}
.inside-gate-before-connect {
  width: 57px;
  height: 16px;
  font-family: "Spindles End";
  font-style: normal;
  font-weight: 1000;
  font-size: 18px;
  line-height: 16px;
  color: #ffffff;
  flex: none;
  order: 0;
  flex-grow: 0;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  text-align: center;
}
